<template>
	<view class="user_tabs content_top">
		<view class="user_tabs_in">
			<view @click="handleClick(index)" :style="{color:current == index?'#F43A3A':'#333333'}" class="tab" v-for="(item,index) in tabs">
				{{item.name}}
				<view class="bottom_line" v-show="current == index"></view>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return {
				current:0
			}
		},
		props:{
			tabs:{
				type:Array,
				default(){
					return []
				}
			}
		},
		created(){
			uni.$on('changeTab',(index)=>{
				this.handleClick(index)
			})
		},
		methods:{
			handleClick(index){
				this.current = index
				uni.$emit('tabChange',index)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.user_tabs{
		background-color: #fff;
		.user_tabs_in{
			height: 88rpx;
			padding: 0 92rpx;
			display: flex;
			justify-content: space-between;
			.tab{
				width: 186rpx;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				position: relative;
         font-size: 30rpx;
				.bottom_line{
					position: absolute;
					width: 100%;
					left: 0;
					bottom:-8rpx;
					border-bottom:8rpx solid #FF0000;
				}
			}
		}
	}
</style>
